import React from "react";
import "./bio.styles.css";
import { Row, Col } from "antd";
export const Bio = ({ bio }) => {
  const { imageURL, content, intro } = bio;
  return (
    <div className="bio">
      <Row>
        <Col>
          <img src={imageURL} alt="me.jpg" />
        </Col>
        <Col>
          <p>{intro}</p>
          <p>{content}</p>
        </Col>
      </Row>
    </div>
  );
};
